@import 'fonts.scss';
@import 'menu-footer.scss';

main{
    font-size: 10px;
}
.main-page-content{
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
}
.main-header-block {
    position: relative;
    background-color: #f8f8f8;
    width: 100%;
    max-height: 810px;

    .main-header-slider-photo{
        position: relative;
        max-width: 1170px;
        img {
            position: relative;
            left: 25%;
            top: -110px;
        }
    }
    .header-mask-letters{
        position: absolute;
        color: rgb(78, 72, 74);
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 100%;
        line-height: 1.2;
        flex: 1 1 auto;
        text-transform: uppercase;
        font-size: 450px;
        font-family: $Proxima;
        font-weight: bold;
        top: 15%;
        left: 0;
        z-index: 1;
        cursor: default;
    }
    .header-button-shop-collection{
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0.5em 0.5em;
        box-sizing: border-box;
        font-family: $Montserrat;
        border: 2px solid rgb(115, 115, 115);
        left: 65%;
        bottom: 15%;
        width: 25%;
        height: 8%;
        z-index: 2;
        cursor: pointer;
        a{
            position: relative;
            display: inline-block;
            font-weight: 500;
            letter-spacing: 0.2em;
            text-transform: uppercase;
            text-decoration: none;
            line-height: 1.2; 
            font-size: 1.6em;  
            color: #505152;
            text-align: center;
        }
        &:hover{
            background-color: #00c8c8;
            border-color: #00c8c8;
            a{
                color: white;
            }
        }
    }
}
.main-items-block{
    position: relative;
    background-color: rgb(255, 255, 255);
    width: 100%;
    font-family: $Montserrat;
    font-weight: 500;
    
    .main-items-wrapper{
        display: flex;
        margin: auto;
        flex-direction: column;
        width: 85%;
        height: 100%;
    }
    .main-items-border-line {
        width: 100%;
        height: 1px;
        margin: 4% 0;
        background-color: rgb(238, 238, 238);
    }
    .main-items-border-line-bottom{
        width: 100%;
        height: 1px;
        margin: 3% 0 4% 0;
        background-color: rgb(238, 238, 238);
    }  
    .main-items-filter-menu{
          ul{
              list-style: none;
              .active{
                color: rgb(0, 200, 200);
                }
              li{
                display: inline-block;
                font-size: 1.3em;
                padding: 0 5% 0 0;
                text-decoration: none;
                text-transform: uppercase;
                color: #7b7b7b;
                border: none;
                outline: none;
                line-height: 1.538;
                cursor: pointer;
            }
        }
    }
    .items-photoes{
        position: relative;
        display: flex;
        width: 100%;
        height: auto;
        justify-content: space-between;
        align-items: center;
        margin: 4% 0 0 0;
        .column {
            width: auto;
            height: auto;
            position: relative;
            .row{
                display: flex;
                justify-content: space-between;
                .photo{
                    .price{
                        position: absolute;
                        top: 2%;
                        left: 3%;
                        font-size: 1.5em;
                        line-height: 1.2;
                        text-transform: uppercase;
                        color: rgb(154, 154, 154);
                    }
                    .photo-thumbs{
                        top: 2%;
                        right: 2%;
                        .thumb{
                            width: 20%;
                            height: 20%;
                        }
                        .thumb-back{
                            margin: 4% 0 0 0;
                            width: 20%;
                            height: 20%;
                        }
                    }
                    .photo-submenu{
                        .submenu-title{
                            font-size: 1em;
                        }
                        .submenu-text{
                            font-size: 1em;
                            padding: 2% 0 2% 0;
                        }
                        .submenu-buttons{
                            width: 40%;
                            .circle {
                                background-color: #a0a0a0;
                                width: 30px;
                                height: 30px;
                                i{font-size: 1.3em;}
                            }
                        }
                    }
                }
            }
            .photo{
                position: relative;
                margin:  0 2% 2% 2%;
                padding: 0 0 0 0;
                img {
                    object-fit: cover;
                    max-width: 100%;
                    height: auto;
                }  
                .price{
                    position: absolute;
                    top: 1.5%;
                    left: 1.6%;
                    font-size: 1.5em;
                    line-height: 1.2;
                    text-transform: uppercase;
                    color: rgb(154, 154, 154);
                    #stock-price{
                        margin: 0 14.5% 0 0;
                        text-decoration: line-through;  
                    }
                }
                .photo-thumbs{
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    display: none;
                    justify-content: baseline;
                    align-items: flex-end;
                    flex-direction: column;
                    z-index: 10;
                    top: 2%;
                    right: 2%;  
                    .thumb-back{
                        margin: 2% 0 0 0;
                        max-width: 100%;
                    }
                    .thumb{
                        max-width: 100%;
                    }
                    .photo-icon{
                        width: 100%;
                        height: 100%;
                        display: none;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        z-index: 10;
                        top: 0;
                        i{
                            font-size: 4em;
                            color: white;
                            cursor: pointer;
                            text-shadow: 0px 2px 12.32px rgba(0, 0, 0, 0.253);
                        }
                    }
                }
                &:hover{
                    box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.14);
                }              
                .photo-submenu{
                position: absolute;
                width: 100%;
                display: none;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                text-align: center;
                padding: 0 0 0 0;
                height: 30%;
                left: 0;
                top: 100%;
                z-index: 6;
                background-color: rgb(255, 255, 255);
                border-top: 1px solid #eeeeee;
                box-shadow: 0px 0px 24px 0px rgba(0, 0, 0, 0.14);
                cursor: default;
                .submenu-title{
                    font-size: 1.3em;
                    font-weight: 500;
                    text-transform: uppercase;
                    color: rgb(114, 114, 114);
                }
                .submenu-text{
                    font-family: $Roboto;
                    font-size: 1.5em;
                    line-height: 1.467;
                    color: rgb(142, 140, 140);
                    padding: 2% 0 2% 0;
                }
                .submenu-buttons{
                    position: relative;
                    display: flex;
                    justify-content: space-evenly;
                    width: 25%;
                    cursor: pointer;
                    .circle {
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        box-sizing: border-box;
                        border-radius: 50%;
                        background-color: #a0a0a0;
                        width: 40px;
                        height: 40px;
                        i{font-size: 2em;
                            color: white;}
    
                        &:hover{
                            background-color: rgb(0, 200, 200);
                        }
                    }
                }
            }      
        }
    }   
    .photo:hover .photo-submenu {
        display: flex;
    }
    .photo:hover .photo-thumbs{
        display: flex;
        .photo-icon{
            display: flex;
        }
    }  
    }
}
.main-look-book{
    position: relative;
    display: flex;
    justify-content: space-between;
    font-family: $Montserrat;
    margin: 0 0 5% 0; 
    .look-book-card-between-line{
        display: block;
        width: 1%;
    }
    .look-book-card{
        position: relative;
        display: flex;
        justify-content: space-between;
        color: #575153;
        background-color: rgb(240, 240, 240);
        padding: 0% 1% 0% 1%;

        .look-book-card-image{
            display: flex;
            margin: 0 0 0 0;
            padding: 5% 0 0 0;
            img{
                max-width: 100%;
                height: auto;
            }
        }     
        .look-book-card-body{
            position: relative;
            display: flex;
            flex-direction: column;
            text-align: right;
            margin: 2% 3%;
            padding: 3% 0 0 0;
            
            .look-book-card-title{
                font-size: 3em;
                line-height: 1.125;
                text-transform: uppercase;

                .look-book-card-title-type{
                    font-weight: 700;
                }
            }
            .look-book-card-text{
                position: relative;
                display: flex;
                flex-direction: column;
                font-family: $Roboto;
                font-size: 1.15em;
                font-weight: 400;
                line-height: 1.467;
                padding: 3% 0 3% 0%;
                .look-book-card-button{
                    font-family: $Montserrat;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: absolute;
                    top: 90%;
                    right: 0;
                    //bottom: -35%;
                    border: 2px solid rgb(115, 115, 115);
                    box-sizing: border-box;
                    -ms-user-select: none; 
                    -moz-user-select: none; 
                    -webkit-user-select: none; 
                    user-select: none; 
                    cursor: pointer;
                    margin: 4% 0 0 0;
                    padding: 3% 7%;
                    .look-book-card-button-link{
                        font-size: 1.3em;
                        font-weight: 500;
                        letter-spacing: 0.2em;
                        text-transform: uppercase;
                    }
                    &:hover{
                        border: 2px solid #00c8c8;
                        background-color: #00c8c8;
                        color: white;
                        .look-book-card-button-link{
                            font-weight: 300;
                        }
                    }
                }
            }
        }
    }
}
  
