        font-size: 9px;
    }
 }
 // Phone adaptive changes
 @media screen and(max-width: 1070px){
    main{
        font-size: 8px;
    }
    .main-header-block {
        max-height: 700px;
        .main-header-slider-photo{
            img {
                left: 10%;
            }
        }
        .header-mask-letters{
            font-size: 400px;
            top: 15%;
            z-index: 0;
        }
        .header-button-shop-collection{
            left: 65%;
            bottom: 15%;
            width: 30%;
            height: 8%;
            z-index: 2;
        }
    }
    .main-items-block{
        font-size: 13px;
        width: 100%;
        .main-items-filter-menu{
            ul{
                display: flex;
                margin: auto;
                justify-content: space-evenly;
                li{
                    padding: 0 0 0 0;
                  font-size: 1em;
              }
          }
      } 
        .items-photoes{
            flex-direction: column;
            justify-content: space-between;
            .column {
                .row{
                    .photo{
                        .price{
                            font-size: 1.5em;
                        }
                        .photo-thumbs{
                            top: 2%;
                            right: 2%;
                            .thumb{
                                width: 20%;
                                height: 20%;
                            }
                            .thumb-back{
                                margin: 4% 0 0 0;
                                width: 20%;
                                height: 20%;
                            }
                        }
                        .photo-submenu{
                            .submenu-title{
                                font-size: 1em;
                            }
                            .submenu-text{
                                font-size: 1em;
                                padding: 2% 0 2% 0;
                            }
                            .submenu-buttons{
                                width: 40%;
                                .circle {
                                    width: 30px;
                                    height: 30px;
                                    i{font-size: 1em;}
                                }
                            }
    
                        }
                    }
                }
                .photo{
                    margin:  0 2% 2% 2%;
                    padding: 0 0 0 0;
                    .price{
                        font-size: 1.5em;
                        line-height: 1.2;
                        #stock-price{
                            margin: 0 14.5% 0 0;
                        }
                    }
                    .photo-thumbs{
                        width: 100%;
                        height: 100%;
                        z-index: 10;
                        top: 2%;
                        right: 2%;            
                        .thumb-back{
                            margin: 2% 0 0 0;
                            max-width: 100%;
                        }
                        .thumb{
                            max-width: 100%;
                        }                
                        .photo-icon{
                            i{
                                font-size: 4em;
                            }
                        }
                    }
                    .photo-submenu{
                        padding: 0 0 0 0;
                        height: 30%;
                        .submenu-title{
                            font-size: 1.6em;
                        }
                        .submenu-text{
                            font-size: 1.4em;
                            line-height: 1.467;
                            padding: 1% 0 1% 0;
                        }
                        .submenu-buttons{
                            width: 25%; 
                            .circle {
                                width: 40px;
                                height: 40px;
                                i{font-size: 1.5em;}
                            }
                        }
                    }      
                }
            }   
        }
    }
    .main-look-book{
        font-size: 13px;
        flex-direction: column;
        align-items: center;
        .look-book-card-between-line{
            width: 100%;
            height: 1em;
        }
        .look-book-card{
            width: 70%;
            padding: 0% 3% 0% 2%;
            .look-book-card-image{
                margin: 0 0 0 0;
                padding: 2% 0 0 0;
                img{
                    max-width: 50%;
                    height: auto;
                }
            }     
            .look-book-card-body{
                width: 50%;
                margin: 6% 0 0 0;
                padding: 0 0 0 0;       
                .look-book-card-title{
                    font-size: 3.3em;
                    line-height: 1.125;
                }
                .look-book-card-text{
                    font-size: 1em;
                    line-height: 1.467;
                    padding: 0% 0 3% 0%;
                    .look-book-card-button{
                        top: 95%;
                        height: 50%;
                        width: 65%;
                        margin: 4% 0 0 0;
                        padding: 5% 5%;
                        border: 1.5px solid rgb(115, 115, 115);
                        .look-book-card-button-link{
                            font-size: 1.1em;
                        }
                    }
                }
            }
        }
    }
 }
